import React from "react"
import { Grid } from "semantic-ui-react"
import { doors } from "../../../doors"
import { colors } from "../../../colors"
import OptionsHeader from "../garageOptions/OptionsHeader"
import MoreOptions from "../garageOptions/MoreOptions"
import Colors from "../garageOptions/Colors"
import Doors from "./Doors"

const ClosetOptions = ({ color, door, dark, onSelect }) => (
  <Grid padded>
    <Grid.Row columns="equal">
      <Grid.Column width={16}>
        <OptionsHeader label="Select Your Door Style" />
      </Grid.Column>
    </Grid.Row>

    <Doors
      door={door}
      doors={doors}
      onSelect={door => onSelect({ color, door })}
    />

    <Colors
      color={color}
      colors={colors}
      dark={dark}
      onSelect={colorId => onSelect({ color: colorId, door })}
    />

    <Grid.Row columns="equal">
      <Grid.Column width={16}>
        <MoreOptions />
      </Grid.Column>
    </Grid.Row>
  </Grid>
)

export default ClosetOptions
