import React from "react"
import { Grid } from "semantic-ui-react"
import { themes } from "../../../themes"
import { colors, flooringColors } from "../../../colors"
import OptionsHeader from "./OptionsHeader"
import OptionsButton from "./OptionsButton"
import MoreOptions from "./MoreOptions"
import Colors from "./Colors"

const GarageOptions = ({ theme, color, floor, onSelect, dark }) => (
  <Grid padded>
    <Grid.Row columns="equal">
      <Grid.Column width={16}>
        <OptionsHeader label="Select Your Theme Garage" />
      </Grid.Column>
    </Grid.Row>
    <Grid.Row
      columns="equal"
      style={{ paddingTop: "6px", paddingLeft: "13px", paddingRight: "8px" }}
    >
      <Grid.Column style={{ padding: 0, paddingRight: "5px" }}>
        <OptionsButton
          label={themes[1]}
          selected={theme === 1}
          onClick={() => onSelect({ theme: 1, color, floor })}
        />
      </Grid.Column>
      <Grid.Column style={{ padding: 0, paddingRight: "5px" }}>
        <OptionsButton
          label={themes[2]}
          selected={theme === 2}
          onClick={() => onSelect({ theme: 2, color, floor })}
        />
      </Grid.Column>
    </Grid.Row>
    <Grid.Row
      columns="equal"
      style={{ paddingTop: "6px", paddingLeft: "13px", paddingRight: "8px" }}
    >
      <Grid.Column style={{ padding: 0, paddingRight: "5px" }}>
        <OptionsButton
          label={themes[3]}
          selected={theme === 3}
          onClick={() => onSelect({ theme: 3, color, floor })}
        />
      </Grid.Column>
      <Grid.Column style={{ padding: 0, paddingRight: "5px" }}>
        <OptionsButton
          label={themes[4]}
          selected={theme === 4}
          onClick={() => onSelect({ theme: 4, color, floor })}
        />
      </Grid.Column>
    </Grid.Row>
    <Grid.Row columns="equal">
      <Grid.Column width={16}>
        <OptionsHeader label="Select Your Cabinetry Color" />
      </Grid.Column>
    </Grid.Row>

    <Colors
      color={color}
      colors={colors}
      dark={dark}
      onSelect={colorId => onSelect({ theme, color: colorId, floor })}
    />

    <Grid.Row columns="equal">
      <Grid.Column width={16}>
        <OptionsHeader label="Select Your Flooring Color" />
      </Grid.Column>
    </Grid.Row>
    <Colors
      color={floor}
      colors={flooringColors}
      dark={dark}
      onSelect={colorId => onSelect({ theme, color, floor: colorId })}
    />
    <Grid.Row columns="equal">
      <Grid.Column width={16}>
        <MoreOptions />
      </Grid.Column>
    </Grid.Row>
  </Grid>
)

export default GarageOptions
