import React from "react"
import { Grid, Segment, Image } from "semantic-ui-react"
import { useMediaQuery } from "react-responsive"
import chunk from "lodash/chunk"
import breakpoints from "../../../breakpoints"

const ResponsiveDoors = props => {
  const isDesktop = useMediaQuery({
    query: `(min-width: ${breakpoints.desktop.min}px)`,
  })
  return isDesktop ? (
    <Doors numberOfChunks={6} {...props} />
  ) : (
    <Doors numberOfChunks={3} {...props} />
  )
}

const Doors = ({ numberOfChunks, door, doors, onSelect }) => (
  <React.Fragment>
    {chunk(doors, numberOfChunks).map(doorsChunk => (
      <Grid.Row columns="equal">
        {doorsChunk.map(({ id, src, label }) => (
          <Grid.Column>
            <Segment
              color={door === id ? "red" : null}
              onClick={() => onSelect(id)}
              style={{ cursor: "pointer", padding: 0 }}
            >
              <Image src={src} />
              <span>{label}</span>
            </Segment>
          </Grid.Column>
        ))}
      </Grid.Row>
    ))}
  </React.Fragment>
)

export default ResponsiveDoors
